.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
html body{
  background-color: rgba(238, 238, 238, 0.871);;
}
.App-container{
  text-align: left;
  margin:auto;
  max-width:1360px;
  width:80%;
  padding:15px;
}
.App-container:after{
  content:"";
  display: table;
  clear: both;
}

.App-container h2{
  margin:10px 0;
}
.filter-btns{
  float: left;
  width:180px;
  background: #fff;
    padding: 12px;
}
.filter-btns p {
  width: 85%;
  margin:auto;
  margin-bottom: 10px;
  border-bottom: 1px solid #ccccccde;
  text-align: center;
}
.true-false-btns{
  float: left;
  width: 100%;
}
.launch_year{
  float: left;
  width:50%;
  text-align: center;
  margin-bottom: 10px;
}
.btns{
  float: left;
  width:50%;
  text-align: center;
  margin-bottom: 10px;
}
.launch_year button{
  background-color:#98ce6978;
  color: #000;
  padding: 2px 12px;
}

.success-launch{
  margin: 12px 0;
  width:100%;
  float:left;
}
.gallery-container{
   padding-left:30px;
  display: flex;
  flex-wrap: wrap;
  float: left;
  width:calc(100% - 180px);
 
}

.details{
  flex: 1 1 25%;
  max-width: 260px;
  margin: 0 10px;
  background: #fff;
  margin-bottom: 20px;
  padding: 12px; 
}

.details-image{
  float: left;
  text-align: center;
  margin-bottom: 10px;
  width:100%;
}
.details-image img{
  width:100%;
}
.details h3{
  color:#1c3448e5;
  font-weight: bold;
}
.details ul li{
  color:#3379b7ea;
}
.details h4{
  color:#000;
  font-weight: bold;
}
.details h4 span{
  color:#3379b7ea;
}
footer{
  float: left;
  width:100%;
}
footer p{
  text-align: center;
}
@media screen and (max-width:767px){
  .App-container{
    width:100%;
  }
  .filter-btns{
    width:100%;
    padding-left: 0;
    flex:1 100%;
    margin:5px 0;
  }
  .gallery-container{
    width:100%;
    padding-left: 0;
    flex:1 100%;
    margin:5px 0;
  }
  .details{
    padding: 10px;
    margin:0;
    max-width: 100%;
    width:100%;
    flex:1 100%;
    margin-bottom: 10px;
   
  }
  footer p b{
    display: block;
  }
}